




































import { Component, Vue } from "vue-property-decorator";
import { LinkText } from "@/models/LinkText";

@Component
export default class AppBar extends Vue {
  item = 1;
  hideButtons = false;
  links: LinkText[] = [
    new LinkText("Головна", "/"),
    new LinkText("Блог", "/news"),
    new LinkText("Заходи", "/events"),
    new LinkText("Про нас", "/about")
  ];

  hideNavButtons(e: Event) {
    const scrollTop = (e.target as Document)?.scrollingElement?.scrollTop;
    if (scrollTop != null) {
      this.hideButtons = scrollTop > 0;
    }
  }
}
